@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
*{
  margin: 0;
  box-sizing: border-box;
}
html{
  font-family: 'Noto Sans JP',sans-serif;
}
body{
  margin: 0;
  background-color: #3f3f3f;
}
.expense_item{
    background-color: #4b4b4b;
    margin: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    overflow: scroll;
    
    
    
}
.expense_item::-webkit-scrollbar{
    display: none;
}
.expense_item h2{
    color: whitesmoke;
    font-size: x-large;
}
.expense_item__desc{
    display: -webkit-flex;
    display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-flex-flow: column-reverse;
          flex-flow: column-reverse;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex: 1 1;
          flex: 1 1;
}
@media (min-width: 580px) {
    
    .expense_item__desc {
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-align-items: center;
              align-items: center;
      margin: 1rem;
      -webkit-justify-content: space-between;
              justify-content: space-between;
    }
    
}
.expense-date{
    background-color: #222222;
    padding: 15px 35px;
    border-radius: 10px;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: whitesmoke;
    text-align: center;
    border: 1px solid whitesmoke;
}
.expense-date__month{
    font-weight: bolder;

}
.expense-date__day{
    font-weight: 900;
    font-size: x-large;
    
}
.expense-amount{
    background-color: #353282;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid whitesmoke;
    font-size: x-large;
    color: whitesmoke;
    cursor: pointer;

}
.expense-filter{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 1rem;
    color: whitesmoke;
    font-family: inherit;
    font-weight: bold;
}
.expense-filter select{
    padding: 5px 10px;
    font-weight: bold;
    border-radius: 8px;
}
.expense-filter select :focus{
    outline: none;
    border: none;
}
.expense{
    background-color: #222222;
    margin: 1rem auto;
    padding: 1rem;
    max-width: 95%;
    width: 50rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 1rem;
}
.expense h3{
    font-family: inherit;
    color: whitesmoke;
}
.new-expense__form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    text-align: left;
    overflow: hidden;
}
.new-expense__control{
    display: block;
    
    
    
}
.new-expense__control input{
    border-radius: 8px;
    padding: 8px;
    border: none;
    font-family: inherit;
    margin-bottom: 1rem;
    margin-top: 1rem;
    width: 20rem;
    display: block;
}
.new-expense__control label{
    font-family: inherit;
    padding: 8px;
    font-weight: bold;
    
}

.new-expense__control input:focus{
    outline: none;
}
.new-expense__action button{
    margin-left: 10px;
}
    
   



.new-expense{
     background-color: rgb(85, 76, 167);
    margin: 1rem auto;
    padding: 1rem;
    max-width: 95%;
    width: 50rem;
    border-radius: 1rem; 
    text-align: center;    
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    
   
}
.new-expense button{
    background-color: #353282;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid whitesmoke;
    font-size: large;
    color: whitesmoke;
    margin-right: 1px;
    cursor: pointer;
    
    
} 


.nav{
    padding: 10px;
    background-color: #222222;
}
.nav h4{
    color: #8c8aee;
    font-size: 30px;    
    text-align: center;
    
}
